    created() {
        // limpa elemento mapa
        this.map = null;
    },
    methods: {
      uploadImage() {
        this.saving = true
        setTimeout(() => this.savedAvatar(), 1000)
      },
      savedAvatar() {
        this.saving = false
        this.saved = true
      },      
      PanhaNhaLocalizason() {      
        let self = this;  
        if (navigator.geolocation) {
          navigator.geolocation.getCurrentPosition(displayLocationInfo);
        }
        function displayLocationInfo(position) {
          const lng = position.coords.longitude;
          const lat = position.coords.latitude;
          self.map.flyTo({
              center: [lng, lat],
              zoom: 14,
          });
          // self.NomeBairro = `longitude: ${ lng } | latitude: ${ lat }`          
          axios.get(`https://api.mapbox.com/geocoding/v5/mapbox.places/${ lng },${ lat }.json?limit=1&access_token=${ self.accessToken }`)
          .then(response => {
            // JSON responses are automatically parsed.
            // this.posts = response.data
            console.log(response.data.features[0].text)
            console.log(response.data.features[0].place_name)
            self.NomeBairro=response.data.features[0].place_name
            self.DadosCoordenadas=response.data.features[0].geometry.coordinates
          })
          .catch(e => {
            this.errors.push(e)
          })
        }
      },
      PanhaLocalizasonNaMapa() {
        let self = this;
        var coordinates = document.getElementById('coordinates');
        var marker = new Mapbox.Marker({
        draggable: true
        })
        .setLngLat([-23.509338075868072, 14.916624983604052])
        .addTo(self.map);
        
        function onDragEnd() {
        var lngLat = marker.getLngLat();
        coordinates.style.display = 'block';
        coordinates.innerHTML =
        'Longitude: ' + lngLat.lng + '<br />Latitude: ' + lngLat.lat;
        console.log('Longitude: ' + lngLat.lng + 'Latitude: ' + lngLat.lat);
        }        
        marker.on('dragend', onDragEnd);
      },
      capture () {
          const mediaStreamTrack = this.mediaStream.getVideoTracks()[0]
          const imageCapture = new window.ImageCapture(mediaStreamTrack)
          return imageCapture.takePhoto().then(blob => {
              firebase.storage.ref().child(`images/picture-${new Date().getTime()}`).put(blob)
                  .then(res =>  {
                      res.ref.getDownloadURL().then((pictureUrl) => {
                          alert("File available at", pictureUrl);
                          this.$router.push({ 
                              name: 'post', 
                              params:{ pictureUrl }
                          })
                      });
                      
                  })
                  .catch(err => {
                      console.log(err)
                      this.$router.go(-1)
                  });
          })
      }
    },
    destroyed () {
        const tracks = this.mediaStream.getTracks()
        tracks.map(track => track.stop())
    },
