          // 'url(https://placekitten.com/g/' +
          // marker.properties.iconSize.join('/') +
          // '/)';
          // markerEl.style.width = marker.properties.iconSize[0] + 'px';
          // markerEl.style.height = marker.properties.iconSize[1] + 'px';

          new Mapbox.Marker(markerEl)
            .setPopup(new Mapbox.Popup({ offset: 25 })
            // .setHTML('<h3>'+marker.properties.title+'</h3><p>'+marker.properties.description+'</p>'))
            .setHTML('<h3 style="background: #d80027;">'+marker.properties.title+'</h3><p>'+marker.properties.description+'</p>'))
            .setLngLat(marker.geometry.coordinates)
            .addTo(self.map);
            markerEl.addEventListener('click', () => {
                self.map.flyTo({
                    center: marker.geometry.coordinates,
                    zoom: 17,
                });
            });
            
        });
          // console.log(this.longitude+', '+this.latitude);
        /* this.map.addControl(
            new Mapbox.GeolocateControl({
                positionOptions: {
                    enableHighAccuracy: true
                },
                trackUserLocation: true
            })
        ); */
        if (navigator.geolocation) {
          navigator.geolocation.getCurrentPosition(displayLocationInfo);
        }
        function displayLocationInfo(position) {
          const lng = position.coords.longitude;
          const lat = position.coords.latitude;
          self.map.flyTo({
              center: [lng, lat],
              zoom: 14,
          });
          // console.log(`longitude: ${ lng } | latitude: ${ lat }`);
        }
         

    },

  watch: {
    avatar: {
      handler: function() {
        this.saved = false
      },
      deep: true
    },
    fab (val) {
      this.tooltips = false
      this.tooltipsDisabled = false
      val && setTimeout(() => {
        this.tooltips = true
        this.$nextTick(() => this.tooltipsDisabled = false)
        console.log("sta funciona")
      }, 250)
    },
    dialogKamera () {
      if(navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
          navigator.mediaDevices.getUserMedia({ video: true })
          .then(mediaStream => {
              this.mediaStream = mediaStream  
              this.$refs.video.srcObject = mediaStream
              this.$refs.video.play()
          })
          .catch(error => console.error('getUserMedia() error:', error))
      }
      else if(navigator.getUserMedia) { // Standard
          navigator.getUserMedia({ video: true }, function(mediaStream) {
              this.mediaStream = mediaStream 
              this.$refs.video.srcObject = mediaStream;
              this.$refs.video.play()
          });
      } else if(navigator.webkitGetUserMedia) { // WebKit-prefixed
          navigator.webkitGetUserMedia({ video: true }, function(mediaStream){
              this.mediaStream = mediaStream 
              this.$refs.video.srcObject = window.webkitURL.createObjectURL(mediaStream);
              this.$refs.video.play()
          });
      } else if(navigator.mozGetUserMedia) { // Mozilla-prefixed
          navigator.mozGetUserMedia({ video: true }, function(mediaStream){
              this.mediaStream = mediaStream 
              this.$refs.video.srcObject = window.URL.createObjectURL(mediaStream);
              this.$refs.video.play()
          });
      }
    },
  }
}
</script>

